.shoppingCart {
    width: 90%;
    text-align: center;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-grow: 0;
    background-color: rgba(0,0,0,0.1);
    padding: 20px;
    margin: 0 auto;
}

.shoppingCart__orders {
    background-color: white;
    flex-basis: 60%;
    padding: 10px;
}

.shoppingCart__summary{
    background-color: white;
    padding: 10px;
    flex-basis: 35%;
    max-height: 40%;
    display: flex;
    flex-direction: column;
}

.shoppingCart__title {
    font-weight: bold;
    font-size: 50px;
}

.shoppingCart__text {
    font-weight: bold;
}

.shoppingCart__container {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    padding-left: 0;
}

.shoppingCart__span {
    color: green;
    font-weight: bold;
    font-size: 20px;
}

.orderElement {
    justify-content: space-around;
    display: flex;
    align-items: center;
    padding: 5px;
    margin-bottom: 10px;
    background-color: rgba(100,100,100,0.6);
    height: 10vh;
    transition: .3s;
}

.orderElement:hover {
    background-color: rgba(100,100,100,0.9);
}

.orderElement__imgWrap {
    height: 90%;
    max-width: 100px;
}

.orderElement__img{
    object-fit: cover;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.orderElement__text {
 font-weight: bold;
 font-size: 15px;
}

.shoppingCart__button {
    /* margin: 30px; */
    padding: 0;
    transition: .3s;
}

.shoppingCart__button:hover {
    background-color: black;
}

.shoppingCart__values {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-bottom: 2px solid black;
}

.shoppingCart__details {
    flex-basis: 60%;
    padding: 10px 0 5px 20px;
    text-align: left;
}

.shoppingCart__detailsValue {
    flex-basis: 25%;
    padding: 10px 20px 0 0;
}

.shoppingCart__payment {
width: 60%;
 background-color: white;
 min-height: 10vh;
 display: flex;
 flex-direction: column;
 margin: 10px 0;
padding: 10px;
}

.shoppingCart__termin {
width: 60%;
min-height: 10vh;
justify-content: last baseline;
background-color: white;
margin: 10px 0;
padding: 10px;
}

.shoppingCart__flexStart {
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    width:100%;
    text-align: left;
}

.shoppingCart__paymentCards {
    display: flex;
    width: 30%;
    justify-content: space-between;
    margin: 0 10px;
    background-color: grey;
    padding: 10px;
}

@media (max-width: 576px){
    .shoppingCart {
        width: 95%;
        flex-wrap: wrap;
    }
    
    .shoppingCart__orders {
        background-color: white;
        flex-basis: 95%;
        margin: 10px auto;
    }
    
    .shoppingCart__summary{
        flex-basis: 95%;
        margin: 0 auto;
    }
    
    .shoppingCart__title {
        font-size: 30px;
    }
    
    .orderElement {
        justify-content: space-around;
        display: flex;
        align-items: center;
        padding: 5px;
        margin-bottom: 10px;
        background-color: rgba(100,100,100,0.6);
        height: 10vh;
        transition: .3s;
    }
    
    .orderElement__text {
     font-size: 12px;
    }
    
    .shoppingCart__button {
        margin: 5px;
        font-size: 15px;
    }
    
    .shoppingCart__details {
        flex-basis: 50%;
    }
    
    .shoppingCart__detailsValue {
        flex-basis: 40%;
    }
    
    .shoppingCart__payment {
    width: 90%;
     margin: 10px auto;
    }
    
    .shoppingCart__termin {
    width: 90%;
    margin: 10px auto;
    }
    
    .shoppingCart__paymentCards {
        width: 80%;
        margin: 0 auto;
    }
 }